
@media screen and (min-width: 300px){
    .wrapper {
        max-width: 960px;
        margin: 0 0;
        box-shadow: 0 5px 30px #dbd6c6;
        padding: 25px 25px;
        background-color: #fffdff;
        font-family: 'Open Sans', sans-serif;
    }
}

@media screen and (min-width: 768px){
    .wrapper {
        max-width: 960px;
        margin: 30px auto;
        box-shadow: 0 5px 30px #dbd6c6;
        padding: 25px 25px;
        background-color: #fffdff;
        font-family: 'Open Sans', sans-serif;
    }
}
@media screen and (min-width: 300px){
    .title {
        text-align: center;
        font-size: 24px;
        color: black;
        margin: 0 0 20px;
        font-family: 'Roboto', sans-serif;
    }
}

@media screen and (min-width: 768px){
    .title {
        text-align: center;
        font-size: 48px;
        color: black;
        margin: 0 0 20px;
        font-family: 'Roboto', sans-serif;
    }
}




.input {
    width: 100%;
    box-sizing: border-box;
    border: 1px solid grey;
    border-radius: 4px;
    height: 30px;
    margin-bottom: 5px;
    padding: 0 10px;
    font-size: 16px;
    outline: none;
}

.tooltip {
    color: red;
    font-size: 12px;
    font-weight: bold;
    margin-top: -16px;
}
.point {
    width: 100%;
    min-height: 34px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 0 10px;
    margin-bottom: 2px;
    background-color: dodgerblue;
    color: white;
    cursor: move;
}

.point.gu-transit {
    background-color: rgba(30, 144, 255, 0.6);
}

.gu-mirror {
    display: none;
}

.pointName {
    cursor: move;
}

.fas.fa-times:hover {
    color: #ffd754;
    cursor: default;
}
@media screen and (min-width: 300px){
    .map {
        width: 100%;
        height: 250px;
    }
}

@media screen and (min-width: 576px){
    .map {
        width: 100%;
        height: 350px;
    }
}


@media screen and (min-width: 768px){
    .map {
        -webkit-flex: 1 1 55%;
                flex: 1 1 55%;
        height: 500px;
    }
}

@media screen and (min-width: 300px){
    .main {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .pointsWrap {
        box-sizing: border-box;
        width: 100%;
        margin-right: 20px;
        padding-top: 10px;
        -webkit-order: 1;
                order: 1;
    }
}

@media screen and (min-width: 768px){
    .main {
        -webkit-flex-direction: row;
                flex-direction: row;
    }

    .pointsWrap {
        -webkit-flex: 1 1 45%;
                flex: 1 1 45%;
        -webkit-order: 0;
                order: 0;
    }
}



body {
    margin: 0;
    padding: 0;
    background-color: rgba(252, 250, 239, 0.83);
}

